














































import loading from 'template/loading'
import goodsCard from 'template/goods_card'
import loadMore from 'template/load_more.vue'
import naData from 'template/no_data'
export default {
	components: {
		goodsCard,
		loadMore,
		naData,
		loading
	},
	data() {
		return {
			currentTab: 0,
			page_num: 1,
			page_size: 5,
			list: [],
			nodata: false,
			nodataText: "还没有相关订单哦~",
			tabs: ['全部', '待付款', '待发货', '待收货', '已完成'],
			loadingType: 0,
			upDate: false,
			popUp: false,
			isLoading: false,
		}
	},
	onLoad(options) {
		if (options.tabIndex) {
			var currentTab = Number(options.tabIndex) + 1
			this.currentTab = currentTab
		}
		this.showLoading()
		this.getList()
	},
	onShow() {
		if (this.upDate) {
			this.list = []
			this.getList()
		}
	},
	onHide() {
		this.popUp = false
	},

	onPullDownRefresh() {
		this.loadingType = 0
		setTimeout(() => {
			this.list = []
			this.page_num = 1
			this.getList()
			uni.stopPullDownRefresh()
		}, 800);
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		var page_num = this.page_num
		this.page_num = this.page_num + 1
		console.log(this.page_num)
		this.getList()

	},
	methods: {
		// tab栏的切换
		tabChange: function(index) {
			this.currentTab = index
			this.loadingType = 0;
			this.page_num = 1
			this.list = []
			this.showLoading()
			this.getList()
		},

		showLoading() {
			this.isLoading = true
			console.log(this.isLoading)
			setTimeout(() => {
				this.isLoading = false
				console.log(this.isLoading)
			}, 800);
		},

		getList() {
			var that = this
			this.nodata = false
			this.$api.get('/api/order/getList', {
				page_num: this.page_num,
				page_size: this.page_size,
				step: this.currentTab
			}, function(res) {
				console.log(res)

				if (res.data.code == 200) {
					var oldList = that.list
					var list = res.data.data
					setTimeout(function(res) {
						if (oldList.length == 0) {
							that.loadingType = 0;
							if (list.length <= 0) {
								that.nodata = true
							} else {
								that.list = list
							}
						} else {
							if (list.length <= 0) {
								that.loadingType = 2;
							} else {
								that.loadingType = 0;
								that.list = oldList.concat(list)
							}
						}
					}, 800)
				}
			})
		},

		//订单操作
		operate(id, index, op) {
			var that = this
			console.log(id, op)
			if (op == 'call_cancel') { //电话取消
				that.popUp = true
				// 					uni.makePhoneCall({
				// 						phoneNumber: '123456789'
				// 					})
			} else if (op == "pay") { //立即付款
				that.doPay(id, index)
			} else if (op == "invoice") { //查看发票
				that.scanInvoice(id, index)
			} else if (op == "cancel") { //直接取消
				uni.showModal({
					title: '确认取消订单吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.sendOP(id, index, op)
						}
					}
				})
			} else if (op == "delete" || op == "remove") {
				uni.showModal({
					title: '确认删除订单吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.sendOP(id, index, op)
						}
					}
				})
			} else if (op == "reset") {
				// that.doPay(id, index)
				that.sendOP(id, index, op)
			} else if (op == "again") {
				that.sendOP(id, index, op)
			} else if (op == "delivery") {
				uni.navigateTo({
					url: 'logistics/logistics?id=' + id
				})
			} else if (op == "confirm") {
				uni.showModal({
					title: '确认收货吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.sendOP(id, index, op)
						}
					}
				})
			}
		},
		sendOP(id, index, op) {
			var that = this
			this.$api.get('/api/order/operate', {
				order_id: id,
				operate: op
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					uni.showToast({
						title: res.data.msg,
					})
					if (op == "again") {
						uni.switchTab({
							url: '/pages/tabbar/tabbar-3/tabbar-3'
						})
					} else if (op == "delete" || op == "remove") {
						var list = that.list
						list.splice(index, 1)
						that.list = list
					} else {
						that.renewRow(id, index)
					}

				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
				}
			})
		},
		doPay: function(id, index) {
			var that = this
			this.$api.post('/api/miniprogram.payment/unifiedOrder', {
				order_id: id
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var payObj = res.data.data;
					payObj.success = function() {
						uni.showToast({
							title: '支付成功!',
						})
						that.renewRow(id, index)
					}
					payObj.fail = function() {
						uni.showToast({
							title: '您取消了支付',
							icon: 'none'
						})
					}
					uni.requestPayment(payObj)
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none'
					})
				}
			})
		},
		renewRow(id, index) {
			this.$api.get('/api/order/renewRow', {
				order_id: id
			}, (res) => {
				if (res.statusCode == 200) {
					var list = this.list;
					list[index] = res.data.data;
					this.list = []
					this.list = list
				}
			})
		},
		hidePop() {
			this.popUp = false
		},

		//查看发票
		scanInvoice(id, index) {
			var filePath = []
			filePath[0] = this.list[index].invoice_file_url
			uni.previewImage({
				current: filePath[0],
				urls: filePath
			});
		}
	}
}
